import {backendlookup} from '../lookup'

export function apiStockLookup(ticker, callback) {
    backendlookup("GET", `/stocks/${ticker}`, callback)
} 

export function apiStockList(callback) {
    const endpoint = '/stocks/'
    backendlookup("GET", endpoint, callback)
}

export function apiStockSearch(searchTerm, callback) {
    const endpoint = '/stocks/search'
    backendlookup("POST", endpoint, callback, {"searchTerm": searchTerm})
}

//TODO add action of getting prediction
export function apiStockAction(ticker, callback) {
    backendlookup("POST", `/stocks/${ticker}/action`, callback)
}