import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
//import App from './App';
import reportWebVitals from './reportWebVitals';

import {StockDetailComponent, StockListComponent, StockSearchComponent} from './stocks'


const e = React.createElement

const stocksListEl = document.getElementById('stocks-list')
if (stocksListEl) {
  ReactDOM.render(e(StockListComponent, stocksListEl.dataset), stocksListEl)
}

const stocksSearchEl = document.getElementById('stocks-search')
if (stocksListEl) {
  ReactDOM.render(e(StockSearchComponent, stocksSearchEl.dataset), stocksSearchEl)
}

const stockDetailElements = document.querySelectorAll(".stock-detail")
stockDetailElements.forEach(container => {
  ReactDOM.render(
    e(StockDetailComponent, container.dataset),
    container);
})

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
