import React, { useState, useEffect } from 'react'

import { apiStockLookup, apiStockSearch } from './lookup'
import { Stock } from './detail'
import { StockList } from './list'
import { ActionButton } from './buttons'


function StockLink (props) {
  const {stock} = props
  const handleStockLink = (event) => {
    window.location.href = `/stocks/${stock.ticker}`
  }
  return <span onClick={handleStockLink}>{stock.ticker}</span>
}
export function StockSearchComponent (props) {
  const [searchTerm, setSearchTerm] = useState("")
  const [searchResults, setSearchResults] = useState([])
  const handleSearchTermChange = (event) => {
    setSearchTerm(event.target.value)
  }

  useEffect(() => {
    const handleSearchTermLookup = (response, status) => {
      if (status === 200) {
        setSearchResults([...response])
      } else {
        alert('Error finding stock')
      }
    }
    apiStockSearch(searchTerm, handleSearchTermLookup)
  }, [searchTerm])

  return <div>
    <input type="text" placeholder="Search" value={searchTerm} onChange={handleSearchTermChange} />
    {searchTerm !== "" && <ul>
      {searchResults.map( (item, index) => (
        <li><StockLink stock={item} key={`${index}-{item.id}`}/></li>
      ))}
    </ul>}
  </div>
}

export function StockListComponent (props) {
  const [newStocks, setNewStocks] = useState([])
  const handleNewStock = (newStock) => {
    let tempNewStocks = [...newStocks]
    tempNewStocks.unshift(newStock)
    setNewStocks(tempNewStocks)
  }
  return <div className={props.className}>
    <StockList newStocks={newStocks} {...props} />
  </div>
}

export function StockDetailComponent(props) {
  console.log('hello')
  const {ticker} = props
  const [didLookup, setDidLookup] = useState(false)
  const [stock, setStock] = useState(null)
  const [isTracking, setIsTracking] = useState(false)

  const handleBackendLookup = (response, status) => {
    if (status === 200) {
      setStock(response)
    } else {
      alert('Error finding stock')
    }
  }

  const handleActionBackend = (response, status) => {
    if (status === 200) {
      setIsTracking(!isTracking)
    } else {
      alert('cant add/remove')
    }
  }

  useEffect(() => {
    if (didLookup === false) {
      apiStockLookup(ticker, handleBackendLookup)
      setDidLookup(true)
    }

  }, [ticker, didLookup, setDidLookup])
  return stock === null ? null :<div> 
    <Stock stock={stock}className={props.className} />
    <ActionButton ticker={stock.ticker} isTracking={isTracking} handleActionBackend={handleActionBackend}/> 
  </div>
}