import React from 'react'

import {apiStockAction} from './lookup'

export function ActionButton (props) {
    const {ticker, isTracking, handleActionBackend} = props

    const handleClick = (event) => {
        event.preventDefault()
        apiStockAction(ticker, handleActionBackend)
    }
    const display = isTracking === true ? 'Remove' : 'Add'
    return <button onClick={handleClick}> {display} </button>
}